.myButton {
    min-width: 122px;/**/
    min-height: 70px;
    text-align: center;
    box-shadow: inset 0 0 0.875em 0.0625em #ffffff;
	// background: linear-gradient(to bottom, #ededed 5%, #dfdfdf 100%);
	background-color: #ededed;
	border-radius: 0.375em;
	border: 0.0625em solid #dcdcdc;
	display: inline-block;
	cursor: pointer;
	color: #000000;
	// font-size: 0.9375em;
    font-size: 17px;
    letter-spacing: 6px;
	padding: 0.375em 1.5em;
	text-decoration: none;
	text-shadow: 0 0.0625em 0 #ffffff;
    background-color: #6f029793;
    color: #FFDF00;
    color: #ffffff;
    border: none;
    text-shadow: 0 0 3px #001604;

}

.myButton:hover{
    background: #58017793;
    color: #FFDF00;
}

//stopwatch
#time{
    display: none;
    letter-spacing: 1px;
    position: absolute;
    top: 88px;
    left: 6px;
    cursor: progress;
    height: 50px;
    font-size: 18px;
    padding: 14px;
    padding-top: 25px;
}

.hide-controls {
    display: none;
}

#resetGame{
    display: none;
    position: absolute;
    letter-spacing: 1px;
    top: 537px;
    left: 499px;
    height: 50px;
 
    font-size: 18px;
    padding: 14px;
    
}

#resetGame:hover{
    animation: shake 1s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    perspective: 1000px;
}



#play_game{
    width: 126px;
    height: 91px;
    position: absolute;
    top: 185px;
    left: 249px;
    color: white;
    border: none;
    letter-spacing: 7px;
    font-size: 21px;
    font-weight: 300;
    
}

// adding shaking effects with key frames
#play_game:hover{
    color: #FFDF00;
    // transform: translateY(1px);
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    perspective: 1000px;
}




.game-counters{
    display: none;
    flex-direction: column;
    position: absolute;
    

    // background: white;
    // width: 200px;
    // height: 200px;
}

#round-counter{
    // animation: moveRound 5s linear 1s infinite alternate;
    // animation: moveRound 5s linear 1s  alternate;
    margin-top: 10px;
    color: #FFDF00;
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0;
    left: 5px;

    cursor: default;
}

.round-counter-animation{
    animation: moveRound 3s linear 0.1s  alternate;
}
.round-counter-animation1{
    animation: moveRound 3s linear 0.1s  alternate;
}

#life-counter{
    margin-top: 10px;
    width: 50px;
    height: 50px;
    position: absolute;
    left: 499px;
    cursor: default;
}

#rage-level{
    margin-top: 10px;
    width: 50px;
    height: 50px;
    position: absolute;
    top: 77px;
    left: 499px;
    
    cursor: default;
}

.rage-level-shake{
    animation: shake 2s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    perspective: 1000px;
    box-shadow: 0 0 50px rgb(225, 18, 18)
}
#cars-passed{
    margin-top: 10px;
    width: 50px;
    height: 50px;
    position: absolute;
    top:157px;
    left: 6px;
    padding-left: 20px;
    cursor: default;
}


.info-screen-div{
    display: none;
    width: 304px;
    height: 190px;
    border-radius: 20px;
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, 0);
    box-shadow: 0 0 50px rgb(225, 18, 18);
    // box-shadow: 0 1px rgba(0, 0, 0, 0.25);
    // border: 1px solid rgba(255,255,255,0.3);
    position: absolute;
    top: 140px;
    left: 162px;
}

.info-screen-div p {
    text-align: center;
    font-size: 25px;
    color: white;
    margin-top: 18px
}

//game instruction tooltip start
.tooltip{
    position: relative;
    
}

.tooltip a{
    color: white;
}

.tooltip a span{
    visibility: hidden;
    cursor: default;
    position: absolute;
    top: -557px;
    left: 439px;
    width: 175px;
    font-size: 16px;
    padding: 11px;
    line-height: 1.4;
    border-radius: 20px;
    backdrop-filter: blur(5px);
    background-color: rgba(255, 255, 255, 0);
    box-shadow: 0 0 50px rgb(236, 40, 219);
}

#info-logo{
    position: absolute;
    top: -615px;
    left: 546px;
    width: 71px;
    border-radius: 10px;
}

.tooltip a:hover span{
    visibility: visible;
}

#info-logo:hover{
    // scale: 1.1;
    cursor: pointer;
    visibility: visible;

    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    perspective: 1000px;
}

//game instruction tooltip end

//sound control
.sound-control{
    width: 40px;
    position: absolute;
    top: 574px;
    left: 15px;
    // margin-right: 35px;
    // margin-top: 30px;
}

.sound-control:hover{
    cursor: pointer;
    // opacity: 0.9;
    // scale: 1.1;
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    perspective: 1000px;
}



@keyframes shake {
    10%, 90% {
      transform: translate3d(-1px, 0, 0);
    }
    20%, 80% {
      transform: translate3d(2px, 0, 0);
    }
    
    30%, 50%, 70% {
      transform: translate3d(-2px, 0, 0);
    }
    40%, 60% {
      transform: translate3d(2px, 0, 0);
  }
}


@keyframes moveRound {
    0%   {left:0px; top:0px;}
    50%  {left:250px; top:0px;}
    100% { left:0px; top:0px;}
  }